body {
    margin: 0;
    font-family: 'Comic Sans MS', sans-serif;
    background: linear-gradient(135deg, #ff9a9e, #fad0c4);
    overflow: hidden;
  }
  
  .home-container {
    position: relative;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }
  
  .container {
    text-align: center;
    color: white;
    padding: 50px;
  }
  
  .title {
    font-size: 2.5rem;
    animation: float 2s infinite ease-in-out;
  }
  
  .subtitle {
    font-size: 1.2rem;
    margin: 10px 0 20px 0;
    animation: fadeIn 3s ease-in-out;
  }
  
  .button {
    font-size: 1.5rem;
    padding: 15px 30px;
    color: #fff;
    background: linear-gradient(45deg, #56ccf2, #2f80ed);
    border: none;
    border-radius: 50px;
    cursor: pointer;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
    transition: transform 0.2s, box-shadow 0.2s;
  }
  
  .button:hover {
    transform: scale(1.1);
    box-shadow: 0 8px 12px rgba(0, 0, 0, 0.3);
  }
  
  .stars {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
  }
  
  .star {
    position: absolute;
    width: 5px;
    height: 5px;
    background: white;
    border-radius: 50%;
    animation: twinkle 4s infinite ease-in-out;
  }
  
  @keyframes twinkle {
    0%, 100% {
      opacity: 0.3;
      transform: scale(0.8);
    }
    50% {
      opacity: 1;
      transform: scale(1);
    }
  }
  
  @keyframes float {
    0%, 100% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-10px);
    }
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  