.camera-container {
    text-align: center;
    background: linear-gradient(to bottom, #fefbd8, #f3d250);
    color: #333;
    padding: 20px;
    font-family: 'Comic Sans MS', cursive, sans-serif;
  }
  
  .camera-title {
    font-size: 28px;
    color: #ff5722;
    margin-bottom: 20px;
  }
  
  .camera-preview {
    border: 5px solid #ffcc80;
    width: 640px;
    height: 360px;
    margin: 0 auto 20px;
    position: relative;
  }
  
  .video-feed {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .capture-button {
    background-color: #ff5722;
    color: white;
    border: none;
    border-radius: 50%;
    width: 70px;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    cursor: pointer;
    margin: 20px auto;
  }
  
  .capture-button:hover {
    background-color: #ff7043;
    transform: scale(1.1);
    transition: 0.3s;
  }
  
  .thumbnail-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 20px;
  }
  
  .thumbnail {
    width: 80px;
    height: 60px;
    margin: 5px;
    border: 2px solid #ffcc80;
    border-radius: 4px;
  }
  